<template>
    <transition
        :duration="animate ? 2500 : 500"
        :enter-class="$style.fadeEnter"
        :enter-to-class="$style.fadeEnterTo"
        :enter-active-class="$style.fadeEnterActive"
        :leave-class="$style.fadeLeave"
        :leave-to-class="$style.fadeLeaveTo"
        :leave-active-class="$style.fadeLeaveActive"
    >
        <div :class="[$style.contentBox, (swipeDirection !== 2) && $style.right]" v-if="animate">
            <h1 v-text="headline"></h1>
            <h2 v-text="subline"></h2>
            <div :class="[$style.container, 'row']">
                <div class="col-6">
                    <img :class="[$style.card, 'img-fluid']" src="../img/visual_card.png">
                </div>
                <div class="col-6 d-flex flex-column">
                    <slot></slot>
                    <button :class="[$style.btn, 'btn w-100 mt-auto']" @click="$store.dispatch('modal/show')">
                        <span>Jetzt Kunde werden!</span>
                    </button>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import { Vue, Component, Prop } from 'vue-property-decorator';
    import { Getter } from 'vuex-class';
    import EventBus from '@/utils/event-bus';

    @Component
    export default class ContentBox extends Vue {
        animate = false;

        @Prop({ type: String }) headline;
        @Prop({ type: String }) subline;

        @Getter('general/getDirection') swipeDirection;

        mounted() {
            setTimeout(() => {
                this.animate = true;
            }, 1500);

            EventBus.$on('PAGE_CHANGING', () => {
                this.animate = false;
            });
        }

        destroyed() {
            EventBus.$off('PAGE_CHANGING');
        }
    }
</script>

