            transition: all 500ms ease-in-out;

            .card {
                transition: all 500ms ease-in-out 500ms;
            }

            .btn {
                transition: all 500ms ease-in-out 500ms;
            }
        }

        &.fade-enter {
            opacity: 0;
            transform: translateX(50px);

            .card {
                opacity: 0;
                transform: translateX(20px);
            }

            .btn {
                opacity: 0;
            }
        }

        &.fade-enter-to {
            opacity: 1;
            transform: translateX(0px);

            .card {
                opacity: 1;
                transform: translateX(0px);
            }

            .btn {
                opacity: 1;
            }
        }

        &.fade-leave-active {
            transition: all 500ms ease-in-out 250ms;

            .card {
                transition: all 500ms ease-in-out;
            }

            .btn {
                transition: all 500ms ease-in-out;
            }
        }

        &.fade-leave {
            opacity: 1;
            transform: translateX(0px);

            .card, .btn {
