                        margin-left: -10px;
                        margin-right: 10px;
                    }
                }
            }

            .btn {
                position: relative;
                margin-top: 30px;
                padding-top: 12px;
                padding-bottom: 12px;
                border-radius: 0;
                color: white;
                background-color: transparent;
                font-family: FuturaStd-Bold;
                animation: pulsate 10s infinite 0.5s;

                span {
                    position: relative;
                    z-index: 3;
                }

                &::before {
                    content: '';
                    position: absolute;
